
.bounce-enter-active {
  -webkit-animation: bounce-in .5s;
          animation: bounce-in .5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  width: 830px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 0;
}
.logo {
  position: relative;
  margin-top: 30px;
}
.logo div {
  margin: 0 auto;
}
.nav {
  margin-top: 20px;
}
ul li {
  list-style: none;
  float: none;
  display: inline-block;
  height: 20px;
}
.nav li a {
  text-decoration: none;
  display: inline;
  font-size: 20px;

  line-height: 20px;
  display: inline-block;
  color: #5d6061;
}
.nav li a.link::after {
  content: "|";
  display: inline-block;
  padding: 10px 15px;
}
.link:hover {
  color: #72c02c;
}

















